export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Name");
  head.push("Week");
  head.push("Syllabus");
  head.push("Teaching plan type");
  head.push("Teaching activities");
  head.push("Evaluation activities");
  head.push("Subject output standards");
  head.push("Created at");
  head.push("Created by");
  head.push("Updated at");
  head.push("Updated by");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.ma);
  row.push(data.ten);
  row.push(data.tuan);
  row.push(`${data.idSyllabus.monHoc.ma} - ${data.idSyllabus.heDaoTao.ten} - ${data.idSyllabus.namHoc.ten}`);
  row.push(data.idLKHGD.ten);
  row.push(data.hoatDongDayHoc.map((item) => item.ten).join(" + "));
  row.push(data.hoatDongDanhGia.map((item) => item.ten).join(" + "));
  row.push(data.chuanDauRaMonHoc.map((item) => item.ma).join(" + "));
  row.push(data.createdAt);
  row.push(data.createdBy ? data.createdBy.email : "Unknown");
  row.push(data.updatedAt);
  row.push(data.updatedBy ? data.updatedBy.email : "Unknown");
  return row;
}
